.about-container {
    padding: 0.5rem;

}

.about-board {
    & > div:first-of-type {
        padding: 0 5px;
    }
    & pre,
    textarea {
        font-family: inherit;
    }

    & pre {
        margin: 0 8.56px;
    }
    & h2 {
        cursor: pointer;
        padding: 0rem 0.5rem;
    }

    & textarea,
    input[type="text"] {
        display: block;
        margin: 0 auto;
        border: none;
        background-color: rgba(9, 30, 66, 0.08);
        border-radius: 3px;

        &:focus {
            outline: none;
        }
    }
    & input[type="text"] {
        display: inline-block;
        height: rem(30px);
        width: 70%;
        margin: rem(16px) 0;
        padding-left: rem(5px);
        font-size: 1rem;
    }

    & .board-creator {
        display: flex;
        align-items: center;
        margin: 10px 0;
        & .member-preview {
            width: 40px;
            height: 40px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            font-size: 1rem;
        }
        & span {
            margin: 0 5px;
        }
    }

    & .board-description {
        padding: 20px 0;
    }
    & .description-header {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    & .save-cancel-btns {
        display: flex;
        justify-content: center;

        & button {
            border: none;
            border-radius: 3px;
            width: 60px;
            padding: 5px 3px;
            margin: 10px;
            cursor: pointer;
        }
    }
}
